.isotopeContainer .title {
  color: #1a1a1a;
  text-align: center;
  margin-bottom: 10px;
}

.isotopeContainer .theme-box {
  position: relative;
}

.isotopeContainer .theme-box .content-overlay {
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  height: 101%;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  -webkit-transition: all 0.4s ease-in-out 0s;
  -moz-transition: all 0.4s ease-in-out 0s;
  transition: all 0.4s ease-in-out 0s;
}

.isotopeContainer .theme-box:hover .content-overlay {
  opacity: 1;
}

.isotopeContainer .content-image {
  width: 100%;
}

.isotopeContainer .content-details {
  position: absolute;
  text-align: center;
  padding-left: 1em;
  padding-right: 1em;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: all 0.3s ease-in-out 0s;
  -moz-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;

  background-size: cover !important;
  background-repeat: no-repeat !important;
  background-position: center top !important;
}

.isotopeContainer .content-details .inner {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  width: 100%;

  padding-top: 40px;
}

.isotopeContainer .theme-box:hover .content-details {
  top: 50%;
  left: 50%;
  opacity: 1;
}

.isotopeContainer .content-details h3 {
  color: #fff;
  font-weight: 300;
  font-size: 18px;
  line-height: 1.3;
  position: relative;
  z-index: 10;
}

.isotopeContainer .content-details h2 {
  color: #fff;
  font-weight: 300;
  font-size: 20px;
  line-height: 1.3;
  position: relative;
  z-index: 10;
}

.isotopeContainer .content-details p {
  color: #fff;
  font-size: 0.8em;
  text-transform: uppercase;
  z-index: 10;
}

.isotopeContainer .inner .buttons {
  position: relative;
  z-index: 10;
}

.isotopeContainer .inner .buttons a {
  color: #2a9eff;
  font-size: 18px;
  text-decoration: underline;
}

.isotopeContainer .inner .buttons a:hover {
  text-decoration: none;
}

.isotopeContainer .fadeIn-bottom {
  top: 40%;
}

.isotopeContainer .fadeIn-top {
  top: 20%;
}

.isotopeContainer .fadeIn-left {
  left: 20%;
}

.isotopeContainer .fadeIn-right {
  left: 80%;
}
