  background: #dcebfe;
  padding: 5px 12px;
}
.form-group .input.inactive-input {
  background-color: #848586 !important;
  pointer-events: none;
  cursor: default;
}
.video-container {
  border-radius: 7px;
  -webkit-border-radius: 7px;
  -moz-border-radius: 7px;
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}
.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.wrap-table-courses {
  max-height: 180px;
  overflow-y: scroll;
}
.wrap-table-courses table {
  width: 100% !important;
}
