#sidebar * {
    white-space: nowrap;
    text-overflow: ellipsis;
}
#sidebar .sub-menu span {
    font-size:11px !important;
    white-space: normal;
    text-overflow: normal;
}
#sidebar .sub-menu li {
    line-height: 1 !important;
}
#sidebar a{
    color:#fff;
    display: block;
    height: 48px;
    overflow: hidden;
	text-decoration: none;
    max-width: 100%;
    width: 100%;
}
#sidebar.hidden{
    max-width: 60px;
}
.sidebar-fixed {
    position: -webkit-sticky;
    position: sticky;
}
.sidebar-fixed.hidden img{
    width: 50px;
}
#sidebar.hidden ul#nav > li > a {

    padding-left:15px !important;
}

#sidebar.hidden #wrap-logo-bottom img{width:30px !important;}

#wrap-logo{
    align-self: flex-start;
    margin: 15px auto 0;
	text-align: center;
}

#wrap-logo a{
    overflow:visible;
    height: 63px;
}

#sidebar #sidebar-content {
    float: left;
    height:100%;
    margin-bottom:20px;
    position: relative;

    width: 100% !important;

}
#sidebar ul#nav {
    list-style: none;
    margin: 0 0 15px;
    padding: 0;
}

#sidebar ul#nav > li > a {
    display: block;
    position: relative;
    margin: 0;
    border: 0;
    border-left:4px solid transparent;
    padding: 17px 22px 17px 45px;
    position: relative;
    font-size: 14px;
    font-weight: 400;
    line-height: 14px;
}
#sidebar ul#nav > li:hover > a{
	background: #0092f7;
