/*Custom Icon Due Tone*/
/* Step 1: Common Duotone Properties: All required to make icons render reliably */

.icon-duotone {
    position: relative;
    padding-left: 1.25em; /* make space for the width of the absolutely positioned icon */
}

/* Set the font-family and font-weight for this style */
.icon-duotone::before,
.icon-duotone::after {
    font-family: "Font Awesome 5 Duotone";
    font-weight: 900;

    /* position both layers of the icon to the left, set our fixed-width width, horizontally center layers, and then vertically align them so they flex with different line heights */
    position: absolute;
    left: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 1.25em;
    text-align: center;
}

/* Set the default opacity levels and colors for each layer */
.icon-duotone::before {
    color: inherit;
    color: var(--fa-primary-color, inherit);
    opacity: 1;
    opacity: 1.0;
    opacity: var(--fa-primary-opacity, 1.0);
}

.icon-duotone::after {
    color: inherit;
    color: var(--fa-secondary-color, inherit);
    opacity: 0.4;
    opacity: var(--fa-secondary-opacity, 0.4);
}

/* Reference Individual Icon's Layers */
.icon-rocket-launch::before {
    content: "\f927";
    color: inherit;
    color: var(--fa-primary-color, inherit);
}

.icon-duotone.icon-rocket-launch::after {
    content: "\10f927";
}
